
main.driverPage, main.admins-page {
    .title__section {
        display: grid;
        grid-template-columns: auto 2fr;
        grid-template-rows: auto 2fr;
        grid-auto-columns: 1fr;
        gap: 16px 35px;
        grid-auto-flow: row;
        grid-template-areas:
            "user-image user-name"
            "user-image buttons"
        ;
        max-width: 730px;
        margin-bottom: 22px;
        h2 {
            font-size: 46px;
            font-weight: 700;
        }
        .user-image { 
            grid-area: user-image;
            height: 57px;
            img {
                width: 160px;
                height: 160px;
            }
        }
        
        .buttons {
            grid-area: buttons;
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            grid-template-rows: 1fr 1fr;
            gap: 15px 18px;
            font-family: 'Roboto', sans-serif;
            .menu-btn {
                width: 228px;
                height: 40px;
            }
            #banDriver {
                width: 270px;
            }
        }
        .user-name { grid-area: user-name; }
        a {
            color: inherit;
            text-decoration: none;
        }
    }
}



main.driverPage, main.stats-page {
    .row {
        display: flex;
        flex-direction: column;
        margin: 29px 0;
        font-family: 'Roboto', sans-serif;
        h2 {
            font-size: 40px;
            font-weight: 700;
        }
    
        .tile__wrapper {
            display: flex;
        }
    
    }
    
    .person-data__section {
        margin-top: 52px;
    }
    
    
    .driver-data__section {
        .info-tile {
            &:first-child {
                background: rgba(219, 243, 217, 1);
            }
            &:last-child {
                background: rgba(215, 231, 255, 1);
            }
        }
    }
    
    .trip-data__section {
        .trip-type__wrapper {
            position: relative;
            display: flex;
            width: 100%;
            margin-top: 34px;
            .trip-type-item {
                display: flex;
                justify-content: center;
                width: 129px;
                padding-bottom: 20px;
                border-bottom: 3px solid transparent;
                font-weight: 700;
                font-size: 15px;
                &.active {
                    border-bottom: 3px solid #3368F2;
                    color: #3368F2;
                    z-index: 10;
                }
            }
        }
        .trip-type__wrapper::after {
            position: absolute;
            content: '';
            background-color:#FFFFFF;
            width: 100%;
            height: 3px;
            bottom: 0;
        }
    
        
        .timetable-title__row {
            .block {
                font-weight: 500;
                span:first-child {
                    margin: 0 73px;
                }
            }
        }
    
        .timetable-row {
            display: flex;
            justify-content: space-between;
            align-items: center;
            height: 52px;
            margin: 2.5px 0;
            padding: 16px 28px;
            border-radius: 10px;
        }
    
        .block {
            span {
                margin: 0 25px;
            }
        }
    
        .timetable-content {
            display: flex;
            flex-direction: column;
            font-weight: 300;
            div.timetable-content__row {
                background-color: white;
            }
            
        }
        .all-trips-btn {
            margin-top: 30px;
            max-width: 248px;
            min-height: 40px;
            box-shadow: none;
        }
    }
    
    .transactions__section {
        max-width: 1200px;
        margin-top: 58px;
        .plates__wrapper {
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-auto-rows: auto;
            gap: 18px 18px;
            .plate-pricing {
                margin-left: 100px;
                margin-right: 0;
                align-items: center;
                font-weight: 700;
            }
        }
        .income {
            color: #5ec556;
        }
        .btn {
            margin-top: 30px;
            max-width: 277px;
            min-height: 40px;
            box-shadow: none;
            font-size: 15px;
        }
    }
}
