$actionBtnBkgColor: rgba(51, 104, 242, 1);
$cancelBtnBkgColor: rgba(29, 29, 68, 1);
$actionBtnShadow: 0px 10px 40px rgba(48, 98, 225, 0.5);
$btnTextColor: rgba(255, 255, 255, 1);
$btnFontSize: 15px;

.btn {
    display: flex;
    justify-content: center;
    align-items: center;
    background: $actionBtnBkgColor;
    box-shadow: $actionBtnShadow;
    border-radius: 40px;
    font-size: $btnFontSize;
    font-weight: 700;
    line-height: 18px;
    color: $btnTextColor;
    cursor: pointer;
    a {
        color: inherit;
        text-decoration: none;
    }
}


.cancel-btn {
    background-color: $cancelBtnBkgColor;
}

