.upload-form-container {
    display: flex;
    min-height: 260px;
    padding: 35px 25px;
    background-color: white;
    font-family: 'Roboto', sans-serif;
    border-radius: 21px;
    & > div {
        margin: 0 10px;
    }
    .left-part {
        position: relative;
        margin-right: 22px;
        flex-basis: 33%;
    }
    .right-part {
        display: flex;
        flex-direction: column;
        flex-basis: 67%;
        margin-left: 22px;
        .text-wrapper {
            display: flex;
            width: 100%;
            margin-bottom: 20px;
            div {
                display: flex;
                flex-direction: column;
            }
            div + div {
                margin-left: 50px;
            }

            span {
                margin: 3px 0;
            }
            span:first-child {
                font-weight: 300;
            }
            span:last-child {
                font-weight: 700;
            }
        }
    }
}