main.all-messages, main.user-messages, main.mailing-page {

    .row-wrapper {
        display: grid;
        grid-auto-rows: 100px 1fr;
        background-color: rgba(255, 255, 255, 1);
        border-radius: 10px;
        padding: 20px;
        padding-bottom: 5px;
    }
    .row {
        display: flex;
        width: 100%;
        min-height: 118px;
        font-size: 15px;
        border-radius: 10px;
        
        img.img-item {
            width: 78px;
            height: 78px;
        }

        .img-wrapper {
            display: flex;
            justify-content: center;
            flex-basis: 5%;
            margin-right: 12.5px;
        }
        .text-wrapper {
            flex-basis: auto;
            line-height: 24px;
            margin: 0 12.5px;
            span {
                display: block;
                margin-bottom: 3px;
            }
            span.message__title {
                font-weight: 700;
            }
            span.message__text {
                font-weight: 300;
            }
        }

        .date-wrapper {
            display: flex;
            justify-content: flex-end;
            flex-basis: 17%;
            margin-left: 12.5px;
            & > span {
                margin-top: 2px;
                margin-right: 10px;
                color: rgba(16, 16, 20, 1);
                font-weight: 300;
            }
            .notification-wrapper {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 32px;
                height: 20px;
                border-radius: 27px;
                background: rgba(255, 63, 63, 1);
                span {
                    color: white;
                    font-weight: 500;
                }
            }
        }
    }
    .img-block {
        display: flex;
        margin-left: 9%;
        margin-bottom: 20px;
        img {
            margin: 0 5px;
            width: 230px;
            height: 165px;
        }
    }
}