.info-plate {
    display: flex;
    width: 592px;
    height: 92px;
    background-color: white;
    align-items: center;
    border-radius: 10px;
    .plate-photo__wrapper {
        margin: 19px 25px;
    }
    .text-content__wrapper {
        flex-direction: column;
        flex-basis: auto;
        span {
            display: block;
            font-size: 15px;
            
            &:first-child {
                margin-top: 23px;
                margin-bottom: 5.5px;
                font-weight: 600;
            }
            &:last-child {
                margin-top: 5.5px;
                margin-bottom: 23px;
                font-weight: 300;
            }
        }

        a {
            color: rgba(51, 104, 242, 1);

        }
        .plate-pricing {
            margin-left: 150px;
            margin-right: 0;
            align-items: center;
            font-weight: 700;
            &.income {
                color: rgba(94, 197, 86, 1);
            }
            color: black;
        }
    }
    img {
        width: 54px;
        height: 54px;
    }
}