.trans-plate-wrapper {
    display: flex;
    max-width: 570px;
    min-height: 120px;
    background-color: white;
    font-family: 'Roboto', sans-serif;
    font-size: 15px;
    border-radius: 10px;
    .part-1 {
        display: flex;
        justify-content: center;
        flex-basis: 30%;
        .img-wrapper {
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: 20px;
            width: 54px;
            height: 54px;
            
            .human-img {
                width: 54px;
                height: 54px;
            }
        }
        .credit-wrapper {
            background: linear-gradient(0deg, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.2)), linear-gradient(0deg, #FDA24F, #FDA24F), #C4C4C4;
            border-radius: 50%;
        }

        .img-wrapper:first-child {
            position: relative;
            margin-right: 20px;
            &::after {
                content: url('./arrow-icon.svg');
                position:absolute;
                right: -15px;
            }
        }


    }

    .part-2 {
        display: flex;
        flex-direction: column;
        flex-basis: 50%;
        font-weight: 300;
        span {
            margin: 10px 0;
        }
        a {
            font-weight: 500;
            color: rgba(51, 104, 242, 1);
        }
    }

    .part-3 {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-basis: 20%;
        font-weight: 700;
    }
}