main.user-messages {
    width: 100%;
    max-height: 86vh;
    font-family: 'Roboto', sans-serif;

    .row {
        max-width: 100%;
    }

    .wrapper {
        max-width: 80%;
        .row-wrapper {
            margin: 20px 0;
        }
        .admin-message {
            background: rgba(51, 104, 242, 1);
            color: rgb(255, 255, 255);
            max-width: 90%;
            margin-left: auto;
            span {
                color: rgb(255, 255, 255);
            }
        }
    }
    .content-container {
        width: 98%;
        max-height: 86vh;
        overflow-y: scroll;
    }
}