main.all-transactions-page {
    max-width: 80%;
    .main__title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 50px;
        margin-bottom: 30px;
        h2 {
            margin-bottom: 10px;
        }
    }

    .content-container {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-auto-rows: 1fr;
        width: 96%;
        gap: 20px;
        margin-top: 30px;
    }
}