$inputWidth: 100%;
$inputHeight: 50px;
$inputBkg: rgba(237, 245, 255, 1);


.text-input-wrapper, .textarea-wrapper {
    width: $inputWidth;
    height: $inputHeight;
    background: $inputBkg;
    margin: 10px 0;
    border-radius: 40px;
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    font-size: 15px;

    input, textarea {
        display: block;
        width: $inputWidth;
        height: $inputHeight;
        padding: 15px 28px;
        background: $inputBkg;
        outline: none;
        border: none;
        border-radius: 40px;
        font-family: 'Roboto', sans-serif;
    }

    textarea {
        resize: none;
    }
    
}

.textarea-written { // add to upload-form-container
    height: 550px;
    max-height: 700px;
    textarea {
        height: 250px;
        border-radius: 15px;
    }

    .textarea-wrapper {
        height: 250px;
    }
    .file-input-wrapper {
        height: 45%;
    }
}