$maxHeight: 86vh;

main.all-messages {
    width: 100%;
    max-height: $maxHeight;
    font-family: 'Roboto', sans-serif;
    .content-container {
        width: 80%;
        max-height: $maxHeight;
        overflow-y: scroll;
    }
    .row-wrapper {
        margin: 15px 0;
    }
    .row {
        max-width: 100%;
    }

    .main__title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        max-width: 1224px;
        h2 {
            margin-bottom: 10px;
            margin-top: 30px;
        }
    }
}