.file-input-wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    width: 100%;
    border: 1px dashed #3368F2;
    border-radius: 14px;
    z-index: 10;
    input[type=file] {
        width: 0.1px;
        height: 0.1px;
        opacity: 0;
        position: absolute;
        z-index: -10;
    }

    label {
        position: absolute;
        display: block;
        width: 100%;
        height: 100%;
        cursor: pointer;
    }

    img {
        margin-top: 51px;
        width: 35px;
        height: 35px;
    }

    img.ready-image {
        position: absolute;
        display: block;
        width: 100%;
        height: 100%;
        margin: 0;
        object-fit: cover; 
    }

    span {
        margin-top: 25px;
    }
}