

.tabbar__title {
    margin-bottom: 42px;
    .heading__wrapper {
        text-align: center;
        position: relative;
        margin-bottom: 10px;
        margin-top: 30px;
    }

    .heading__wrapper::after {
        content: '';
        position: absolute;
        top: 21px;
        bottom: 0;
        right: -3px;
        display: block;
        border-radius: 2px;
        width: 100px;
        height: 4px;
        background-color: $logoCross;
    }

    .text-wrapper {
        text-align: center;
        span {
            font-weight: 400;
            font-size: 15px;
        }
    }
}