$rowGradient: linear-gradient(83.56deg, #E5EFFF 54.25%, #E5FFEC 97.44%);
$scrollThumbBkg: rgba(51, 104, 242, 1);

main.allUsers {
    width: 99.4%;
    
    .main__title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        max-width: 1224px;
        h2 {
            margin-bottom: 10px;
            margin-top: 30px;
        }
    }

    .main__content {
        display: flex;
        flex-direction: column;
        color: black;
        max-height: 1100px;
        overflow-y: scroll;

        .row {
            background: $rowGradient;
            font-weight: 400;
        }
        .row:nth-child(even) {
            background: rgba(255, 255, 255, 1);
        }

        .row-item {
            font-size: 15px;
        }
    }

    .main__content::-webkit-scrollbar {
        width: 5px;
        border-radius: 26px;
        background: rgba(255, 255, 255, 1);
    }

    .main__content::-webkit-scrollbar-thumb {
        background: $scrollThumbBkg;
        border-radius: 26px;
    }
}