div.info-tile {
    display: inherit;
    flex-direction: column;
    justify-content: center;
    margin: 26px 8px;
    height: 102px;
    width: 248px;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 1); 

    &:first-child {
        margin-left: 0;
    }
    span:first-child {
        margin: 28px auto 5.5px 28px;
        font-weight: 400;
    }
    span:last-child {
        margin: 5.5px auto 28px 28px;
        font-weight: 700;
    }
}