$bebasNeue: 'Bebas Neue', cursive;
$bodyBkg: rgba(246, 250, 255, 1);
$bodyEllipse: rgba(224, 255, 243, 0.2);
* {
    box-sizing: border-box;
}
h2 {
    font-family: $bebasNeue;
    font-size: 50px;
    margin: 0;
    padding: 0;
}
body {
    background: $bodyBkg;
}

body::before {
    content: '';
    position: absolute;
    height: 1092px;
    width: 1092px;
    left: -15px;
    top: 236px;
    border-radius: 100%;
    background: $bodyEllipse;
    filter: blur(80px);
    z-index: -10;
}


.container {
    display: grid;
    grid-template-columns: auto 3fr;
    grid-template-rows: 1fr;
    gap: 5vw;
}