


.tabbar__exit {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    position: absolute;
    bottom: 1.5vh;
    .exit__text {
        display: block;
        font-weight: 500;
        font-size: 15px;
    }

    .exit__btn {
        display: block;
        margin-top: 20px;
        width: 189px;
        height: 40px;
        background: $exitBtnBkg;
        border-radius: 23px;
        color: $exitBtnColor;
        font-weight: 700;
        outline: none;
        letter-spacing: 2px;
    }

}