main.admins-page {
    width: 80%;
    .main__title { 
        display: flex;
        justify-content: space-between;
        align-items: center;
        max-width: 1224px;
        h2 {
            margin-bottom: 30px;
            margin-top: 30px;
        }
        font-family: 'Roboto', sans-serif;
    }

    .title__section {
        max-width: 100%;
        background-color: white;
        border-radius: 10px;
        padding: 25px;
        .user-image {
            img {
                width: 78px;
                height: 78px;
            }
        }
        h2 {
            font-size: 26px;
        }
        .buttons {
            grid-template-columns: repeat(3, 270px);
            grid-template-rows: 70px 70px;
            gap: 30px 25px;
        }
    }

    .admins-btn {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 49px;
        width: 262px;
        border-radius: 40px;
        margin: 0;
        padding: 10px;
        cursor: pointer;
        span {
            color: white;
            font-weight: 700;
        }
    }
}