main.allUsers {
    div.row {
        display: grid;
        grid-template-columns: 0.5fr 2fr 1fr 1fr 0.6fr 1fr;
        gap: 0 50px;
        align-items: center;
        height: 52px;
        margin: 3px 0;
        padding: 0 0 0 23px;
        border-radius: 10px;
        background: transparent;
        font-family: 'Roboto', sans-serif;
        font-weight: 500;
        max-width: 1224px;
    }
}