main.AllTransactions {
    .main__title { 
        display: flex;
        justify-content: space-between;
        align-items: center;
        max-width: 1224px;
        h2 {
            margin-bottom: 45px;
            margin-top: 30px;
        }
        font-family: 'Roboto', sans-serif;
    }

    .container {
        display: grid; 
        max-width: 1224px;
        grid-auto-rows: 1fr; 
        grid-template-columns: 1fr 1fr; 
        gap: 18px 18px; 
        font-family: 'Roboto', sans-serif;
        .info-plate {
            width: 600px;
            .plate-pricing {
                margin-left: 100px;
                margin-right: 0;
                align-items: center;
                font-weight: 700;
            }
        }
        .income {
            color: #5ec556;
        }
    }
    .item {
        background-color: red;
    }
}