main.mailing-page {
    width: 80%;
    font-family: 'Roboto', sans-serif;
    div.row-wrapper {
        max-width: 100%;
        margin: 20px 0;
    }
    .main__title {
        margin-top: 50px;
        margin-bottom: 30px;
        h2 {
            margin-bottom: 10px;
        }
    }
    .main__text {
        font-weight: 300;
    }

    .admin-message {
        background: rgba(51, 104, 242, 1);
        color: rgb(255, 255, 255);
        max-width: 90%;
        margin-left: auto;
        font-size: 15px;
        letter-spacing: 1.1px;
        span.date-text {
            color: rgb(255, 255, 255);
        }
    }
}