.breadcrumbs__container {
    display: flex;
    min-width: 557px;
    margin: 50px 0 40px 0;
    .back-btn {
        cursor: pointer;
    }
    div {
        position: relative;
        display: flex;
        align-items: center;
        height: 44px;
        width: 131px;
        border-radius: 57px;
        background: rgba(255, 255, 255, 1);
        box-shadow: 0px 10px 40px rgba(48, 98, 225, 0.2);
        
        img {
            position: absolute;
            display: block;
            width: 9px;
            height: 16px;
            left: 25px;
        }

        span {
            position: absolute;
            display: block;
            right: 36px;
            font-size: 15px;
            font-weight: 500;
            cursor: pointer;
        }
    }
}