main.ads-page {
    width: 100%;
    max-height: 100vh;
    .main__title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        max-width: 1224px;
        h2 {
            margin-bottom: 10px;
            margin-top: 30px;
        }
        h2.subtitle {
            font-size: 46px;
        }
    }
    .wrapper {
        width: 100%;
        max-height: 79vh;
        overflow: scroll;
    }
    .trip-type-item {
        font-size: 15px;
    }
    .content-container {
        margin-top: 20px;
        border-radius: 21px;
    }
    .ready-container {
        margin-top: 20px;
    }
    .content-container, .ready-container {
        max-width: 80%;
    }

    .btn-container {
        // position: absolute;
        display: flex;
        justify-content: flex-start;
        width: 100%;
        margin-top: 20px;
        .btn {
            width: 200px;
            height: 50px;
        }

        .btn + .btn {
            margin-left: 20px;
        }
    }
}