.trip-type__wrapper {
    position: relative;
    display: flex;
    width: 100%;
    margin-top: 34px;
    font-family: 'Roboto', sans-serif;
    .trip-type-item {
        display: flex;
        justify-content: center;
        width: 129px;
        padding-bottom: 20px;
        border-bottom: 3px solid transparent;
        font-weight: 700;
        font-size: 15px;
        cursor: pointer;
        &.active {
            border-bottom: 3px solid #3368F2;
            color: #3368F2;
            z-index: 10;
        }
    }
}
.trip-type__wrapper::after {
    position: absolute;
    content: '';
    background-color:#FFFFFF;
    width: 100%;
    height: 3px;
    bottom: 0;
}