.checkbox-wrapper {
    display: flex;
    align-items: center;
    margin-top: 30px;
    font-weight: 300;
    font-size: 15px;
    font-family: 'Roboto', sans-serif;
}

// checkbox
.onoffswitch {
    position: relative; 
    width: 70px;
    margin-right: 20px;
    -webkit-user-select:none; 
    -moz-user-select:none; 
    -ms-user-select: none;
}
.onoffswitch-checkbox {
    position: absolute;
    opacity: 0;
    pointer-events: none;
}
.onoffswitch-label {
    display: block; 
    overflow: hidden; 
    cursor: pointer;
    // border: 2px solid #999999;
    border-radius: 70px;
}
.onoffswitch-inner {
    display: block; 
    width: 200%; 
    margin-left: -100%;
    transition: margin 0.3s ease-in 0s;  //background-green transition
}
.onoffswitch-inner:before, .onoffswitch-inner:after {
    display: block; 
    float: left; 
    width: 50%; 
    height: 41px; 
    padding: 0; 
    line-height: 41px;
    font-size: 10px; 
    color: white; 
    box-sizing: border-box;
}
.onoffswitch-inner:before {
    content: "";
    padding-left: 5px;
    background-color: #31D27B; color: #FFFFFF;
}
.onoffswitch-inner:after {
    content: "";
    padding-right: 5px;
    background-color: #CEDBE8;
    text-align: right;
}
.onoffswitch-switch {
    display: block; 
    width: 33px; 
    margin: 4px;
    background: #FFFFFF;
    position: absolute; 
    top: 0; 
    bottom: 0;
    right: 27px;
    // border: 2px solid #999999; 
    border-radius: 70px;
    transition: all 0.3s ease-in 0s; // round ball trans
}
.onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-inner {
    margin-left: 0;
}
.onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-switch {
    right: 0px; 
}

// checkbox-end