$inputWidth: 1200px;
$inputHeight: 60px;

.input-container {
    position: fixed;
    bottom: 40px;
    display: flex;
    max-width: 63vw;
    align-items: center;
    .input-wrapper {
        position: relative;
        width: $inputWidth;
        height: $inputHeight;
        box-shadow: 0px 10px 40px rgba(48, 98, 225, 0.2);
        background: rgba(255, 255, 255, 1);
        border-radius: 19px;
        padding: 18px 0 18px 30px;
        margin-right: 20px;
        font-family: 'Roboto', sans-serif;
        input {
            width: 100%;
            height: 100%;
            background: inherit;
            border: none;
            outline: none;
            border-radius: 19px;
            font-family: 'Roboto', sans-serif;
            font-size: 15px;
            font-weight: 300;
        }
        .img-wrapper {
            position: absolute;
            top: 20px;
            right: 22px;
            img {
                width: 18px;
                height: 20px;
            }
        }
    }


    .attach-wrapper {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 60px;
        height: 60px;
        background-color: rgba(255, 255, 255, 1);
        border-radius: 16px;
        img {
            width: 20px;
            height: 20px;
        }
    }
}