nav {
    width: 100%;
    max-height: 500px;
    margin: 42px 0 250px 30px;
    ul {
        display: flex;
        flex-direction: column;
        justify-content: center;
        list-style: none;
        padding: 0 0 0 20px;
        margin: 0;
        
    }
    .nav__list-item {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        align-content: center;
        width: $nav_block-width;
        height: $nav_block-height;
        padding: 25px 0 25px 15px;
        img {
            width: 20px;
            height: 20px;
            margin-right: 15px;
        }
        
        span {
            font-weight: 700;
            font-size: $nav_font-size;
            margin-top: 5px;
            font-family: $bebasNeue;
            a {
                color: inherit;
                text-decoration: none;
            }
        }
        & + .nav__list-item, &:first-child {
            border-bottom: 1px solid $navItemBorderColor;
        }

        &:last-child {
            border: none;
        }
    }
    
}