ul.breadcrumbs__list {
    display: flex;
    list-style: none;
    font-family: 'Roboto', sans-serif;
    li {
        position: relative;
        margin-right: 6.5px;
    }
    li + li {
        margin: 0 6.5px;
    }

    li + li::before {
        content: '-';
        position: absolute;
        left: -9px;
        color: black;
    }
    a {
        color: black;
        text-decoration: none;
        font-weight: 300;
    }
    a.active {
        text-decoration: underline;
        font-weight: 600;
    }
}