$input-width: 468px;
$input-height: 47px;

.main__search {
    position: relative;
    width: $input-width;
    height: $input-height;
    background-color: white;
    border-radius: 30px;

    input {
        width: $input-width;
        height: $input-height;
        background: transparent;
        border: none;
        outline: none;
        padding: 0 0 0 20px;
        font-family: 'Roboto', sans-serif;
        font-size: 15px;
        font-style: normal;
        font-weight: 300;
    }

    img {
        position: absolute;
        top: 15px;
        bottom: 0px;
        right: 20px;
        width: 19px;
        height: 19px;
    }
}