.ride-tile {
    display: flex;
    flex-direction: column;
    background-color: white;
    max-width: 100%;
    width: 100%;
    height: 182px;
    border-radius: 10px;
    margin: 10px 0;
    div.row {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        height: 50%;
        // margin: 10px 0;
        padding: 28px;
        padding-right: 50px;
        font-family: 'Roboto', sans-serif;
        font-size: 15px;
        font-weight: 500;
        img {
            display: block;
            width: 56px;
            height: 56px;
            margin: 0 5px
        }
        .content-side {
            display: flex;
            .driver-text {
                margin-left: 15px;
                a {
                    color: rgba(51, 104, 242, 1);
                    font-weight: 700;
                }
            }
            .content-item {
                position: relative;
                display: flex;
                flex-direction: column;
                span {
                    margin: 5px 0;
                }
                span:last-child {
                    font-weight: 300;
                }
                img {
                    position: absolute;
                    top: 5px;
                    left: 0;
                    width: 13px;
                    height: 15px;
                }
            }

            .address {
                position: relative;
                padding-left: 30px;
                &:first-child::after {
                    content: '- - - - - -';
                    position: absolute;
                    min-width: 24px;
                    font-size: 13px;
                    top: 0.4rem;
                    right: -3.7em;
                }
            }
            .address + .address {
                margin-left: 50px;
            }
        }

        .info-wrapper {
            display: flex;
            width: 380px;
            justify-content: space-between;
        }
    }
}