$asideColor: white;
$nav_font-size: 18px;
$nav_block-width: 85%;
$nav_block-height: 70px;
$input-width: 468px;
$input-height: 47px;
$logoCross: rgba(253, 162, 79, 1);
$navItemBorderColor: rgba(240, 240, 240, 1);
$activeColor: blue; // see .active at the end
$exitBtnBkg: #1D1D44;
$exitBtnColor: white;
$rowGradient: linear-gradient(83.56deg, #E5EFFF 54.25%, #E5FFEC 97.44%);
$scrollThumbBkg: rgba(51, 104, 242, 1);
$bebasNeue: 'Bebas Neue', cursive;
$roboto: 'Roboto', sans-serif;



aside {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
    min-width: 15vw;
    background-color: $asideColor;
    box-shadow: 0px 10px 40px 0px #3062E133;
    font-family: $roboto;
    position: relative;
    span {
        font-size: $nav_font-size;
    }

    .active {
        color: $activeColor;
        img {
            filter: invert(31%) sepia(87%) saturate(3890%) hue-rotate(220deg) brightness(104%) contrast(90%);
        }
    }
}


@import './components/footer';
@import './components/title';
@import './components/TabbarNavigation/nav';