$firstTileBkg: rgba(219, 243, 217, 1);
$firstTileColor: rgba(92, 163, 86, 1);
$secondTileBkg: rgba(215, 231, 255, 1);
$secondTileColor:   rgba(51, 104, 242, 1);
$thirdTileBkg:  rgba(249, 235, 222, 1);
$thirdTileColor:    rgba(51, 104, 242, 1);
$fourthTileBkg: rgba(43, 43, 56, 1);
$fourthTileColor: rgba(255, 255, 255, 1);
$othersTilesBkg: rgba(255, 255, 255, 1);
$othersTilesColor: rgba(16, 16, 20, 1);



main.stats-page {
    width: 80%;
    .main__title { 
        display: flex;
        justify-content: space-between;
        align-items: center;
        max-width: 1224px;
        h2 {
            margin-bottom: 30px;
            margin-top: 30px;
        }
        font-family: 'Roboto', sans-serif;
    }

    .tile__wrapper {
        flex-wrap: wrap;
        
        .info-tile {
            margin: 10px 10px;
            width: 270px;
            height: 120px;
            span:first-child {
                font-weight: 500;
            }
            span:last-child {
                font-family: "Bebas Neue", cursive;
                font-size: 36px;
            }
        }
        .info-tile:first-child {
            background: $firstTileBkg;
            span:last-child {
                color: $firstTileColor;
            }
        }
        .info-tile:nth-child(2) {
            background: $secondTileBkg;
            span:last-child {
                color: $secondTileColor;
            }
        }
        .info-tile:nth-child(3) {
            background: $thirdTileBkg;
            span:last-child {
                color: $thirdTileColor;
            }
        }
        .info-tile:nth-child(4) {
            background: $fourthTileBkg;
            span:first-child {
                color: white;
            }
            span:last-child {
                color: $fourthTileColor;
            }
        }
        .info-tile:nth-child(5), .info-tile:nth-child(6), .info-tile:nth-child(7) {
            background: $othersTilesBkg;
            span:last-child {
                color: $othersTilesColor;
            }
        }
    }
}
