main.sales-page {
    width: 80%;
    .main__title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        max-width: 1224px;
        h2 {
            margin-bottom: 10px;
            margin-top: 30px;
        }
    }

    .content-container {
        margin-top: 20px;
        max-width: 100%;
        border-radius: 21px;
    }
    .ready-container {
        margin-top: 20px;
        .right-part {
            position: relative;
        }
    }

    .btn-container {
        // position: absolute;
        display: flex;
        justify-content: flex-start;
        width: 100%;
        margin-top: 20px;
        .btn {
            width: 200px;
            height: 50px;
        }

        .btn + .btn {
            margin-left: 20px;
        }
    }
}