.form__input-wrapper {
    display: inherit;
    flex-direction: column;
    margin-right: 14px;
    margin-bottom: 14px;
    label {
        margin-bottom: 13px;
        color: rgba(87, 87, 114, 1);
        font-weight: 300;
    }
    input {
        padding-left: 30px;
        height: 49px;
        width: 262px;
        border-radius: 59px;
        background: rgba(233, 242, 255, 1);
        color: rgba(16, 16, 20, 1);
        border: none;
        outline: none;
        font-weight: 500;
    }
}