$roboto: 'Roboto', sans-serif;
$bebas: 'Bebas Neue', cursive;



.popup {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1050;
    // background-color: rgba(0, 0, 0, 0.5);
    background: rgba(29, 29, 68, 0.3);
    p, span, input, label {
        font-family: $roboto;
        font-weight: 400;
        font-size: 15px;
    }

    h2 {
        margin: 15px 0 19px 0;
        padding: 0;
        font-family: $bebas;
        font-weight: 700;
        font-size: 46px;
    }
    
}

.popup .popup__content {
    /* display: none; */
    display: flex;
    flex-direction: column;
    align-items: center;
    position: fixed;
    top: 10%;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    min-width: 712px;
    min-height: 359px;
    background-color: #ffffff;
    border-radius: 22px;
    .popup-row {
        display: flex;
    }
    .popup-row:nth-child(2) {
        display: flex;
        flex-direction: column;
        align-items: center;
        form {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }
    img.editUserImg, img.popup__img {
        width: 110px;
        height: 110px;
    }
    img.popup__img {
        margin-top: -54px;
    }
    .popup__close {
        position: absolute;
        top: 20px;
        right: 20px;
        img {
            width: 20px;
            height: 20px;
        }
        cursor: pointer;
    }
    .popup__text {
        margin: 0;
        text-align: center;
        width: 352px;
        height: 75px;
        line-height: 25px;
    }
    .popup__btn {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 48px;
        width: 231px;
        border-radius: 40px;
        margin: 29px 10px 60px 10px;
        cursor: pointer;
        span {
            color: white;
            font-weight: 700;
        }
        &:first-child {
            box-shadow: 0px 10px 40px rgba(48, 98, 225, 0.5);
        }
        
    }


    


    
    
    
}


.editUser, .addAdmin {
    .popup-row {
        // width: 100%;
        padding: 0 49px;
        .popup__title {
            display: block;
            text-align: center;
            // margin: 67px 193px 7.5px 193px;
            margin: 50px 0 30px 0;
            min-width: 528px;
        }
        
        form {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            grid-template-rows: 1fr 1fr;
            
        }
        button.btn {
            // position: absolute;
            top: 200px;

            display: flex;
            justify-content: center;
            align-items: center;
            background: rgba(51, 104, 242, 1);
            box-shadow: 0px 10px 40px rgba(48, 98, 225, 0.5);
            border-radius: 40px;
            font-weight: 700;
            line-height: 18px;
            cursor: pointer;
            outline: none;
            border: none;
            a {
                color: inherit;
                text-decoration: none;
            }
        }
        .popup__btn {
            background: rgba(51, 104, 242, 1);
            box-shadow: 0px 10px 40px rgba(48, 98, 225, 0.5);
        }   
    }

    .popup-row:nth-child(2) {
        // display: flex;
        // flex-direction: column;
        // align-items: center;
        padding: 0;
    }
}
.changePassword {
    .form__input-wrapper {
        input.changeUserPassword {
            width: 482px;
        }
    }
    
}


.hide {
    display: none;
}

.show {
    display: block;
}







